.program {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  background-color: #fff;
  margin: 0 auto;
  align-items: center;
}

.program__weight {
    color: #111111;
    font-family: "Oswald", "Arial", sans-serif;
    background-color: #f2f2f2;
    min-width: 280px;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 266px;
    margin-top: 0px;
  }

  .weight--pro {
    margin-top: 20px;
  }

  .weight h3 {
    font-family: "Oswald", "Arial", sans-serif;
    text-transform: uppercase;
    padding: 6px 0 0 57px;
    margin-left: 13px;
    font-weight: normal;
    position: relative;
    font-size: 24px;
  }

  .weight__description {
    font-family: "Arial", sans-serif;
    border-bottom: 1px solid #d9d9d9;
    font-size: 14px;
    line-height: 18px;
    max-width: 238px;
    padding: 5px 0 23px 0px;
    margin-left: 0px;
  }

  .weight__link {
    font-family: "Oswald", "Arial", sans-serif;
    text-decoration: none;
    color: #111111;
    text-transform: uppercase;
    margin-left: 0px;
    position: relative;
    padding: 0;
  }

  .weight__link::after {
    content: url(../img/icon-right-arrow-long.svg);
    position: absolute;
    width: 25px;
    left: 95px;
  }

  .weight__link:hover::after {
    content: url(../img/icon-right-arrow-verylong.svg);
    position: absolute;
    width: 33px;
    left: 130px;
  }

  .weight__link:active {
    opacity: 0.3;
  }

  .weight__link:active::after {
    content: url(../img/icon-right-arrow-verylong.svg);
    position: absolute;
    width: 33px;
    left: 130px;
    opacity: 0.3;
  }

  .weight__slim::before {
    content: "";
    width: 50px;
    height: 50px;
    background-color: #68b738;
    position: absolute;
    left: -12px;
    top: 0px;
    border-radius: 50%;
  }

  .weight__slim::after {
    content: url(../img/icon-cat-slim.svg);
    width: 36px;
    position: absolute;
    left: -4px;
    top: -3px;
  }

  .weight__pro::before {
    content: "";
    width: 50px;
    height: 50px;
    background-color: #68b738;
    position: absolute;
    left: -13px;
    top: 0px;
    border-radius: 50%;
  }

  .weight__pro::after {
    content: url(../img/icon-cat-pro.svg);
    width: 66px;
    position: absolute;
    left: -21px;
    top: 10px;
  }

